import * as React from 'react'
import { connect } from 'react-redux'
import {
  DialogActions,
  DialogTitle,
  IconButton,
  DialogContent,
  DialogContentText,
  TextField,
  Button,
  Dialog,
  Theme,
  createStyles,
  withStyles,
  WithStyles,
  Typography,
  Paper
} from '@material-ui/core'
import { Close, Delete, Add, Clear } from '@material-ui/icons'
import { cloneDeep } from 'lodash'
import { setTaskA } from '../../store/actions/task'
import { TTask } from '../../types/task'
import { ChooseColor } from '../utils/chooseColor'
import { TState } from '../../types/state'
import { getAllColumnsArr } from '../../utils/utilities'
import { useState } from 'react'
import { TaskModalDateField } from './DateField'
import { TaskModalChangeTasksColumnField } from './changeTasksColumn'
import { setTagA } from '../../store/actions/tags'
import { CreateTag } from './createTag'
import { addDays } from 'date-fns'
import { TaskModalComments } from './Comments'
import { TaskModalTags } from './Tags'
import { TaskModalManualTimeEntry } from './manualEntry'
import { TaskModalSubtasks } from './Subtasks'
import uuid from 'uuid'
import { GQL_DELETE_TASK, GQL_EDIT_TASK } from '../../graphql/mutations/task'
import { MutationResult, Mutation } from 'react-apollo'
import {
  DeleteTaskMutationVariables,
  DeleteTaskMutation,
  EditTaskMutation,
  EditTaskMutationVariables
} from '../../graphql/types'
import { setProjectA } from '../../store/actions/project'

type TaskModalProps = ReturnType<typeof mapState> &
  typeof actionCreators &
  OwnProps &
  WithStyles<typeof styles>

interface OwnProps {
  task: TTask
  projectId: string
  onClose(): void
}

const styles = (theme: Theme) =>
  createStyles({
    closeButton: {
      position: 'absolute',
      right: 8,
      top: 8,
      color: theme.palette.grey[500]
    },
    picker: {
      width: '100%'
    }
  })

const CTaskModal = (props: TaskModalProps) => {
  const [task, setTask] = useState(cloneDeep(props.task))
  const [anchor, setAnchor] = useState(null as HTMLElement | null)

  const project = props.projects[props.projectId]

  const tasksColumn = getAllColumnsArr(props.projects).find(
    col => col.taskIds.indexOf(props.task.id) !== -1
  )

  const [extraHours, setExtraHours] = useState(0)
  const [extraMinutes, setExtraMinutes] = useState(0)

  const [hasDate, setHasDate] = useState(props.task.dueDate !== undefined)
  const [columnId, setColumnId] = useState(
    tasksColumn ? tasksColumn.id : undefined
  )

  const { classes, projects, onClose } = props

  return (
    <>
      <Dialog open={true} fullWidth maxWidth={'md'} onClose={onClose}>
        <Mutation
          onCompleted={({ editTask }) => {
            if (editTask && editTask.task) {
              console.log(props.projectId)

              props.setTaskA({
                id: editTask.task.id,
                newTask: editTask.task,
                projectId: props.projectId
              })

              props.onClose()
            }
          }}
          mutation={GQL_EDIT_TASK}
        >
          {(
            editTask: (args: { variables: EditTaskMutationVariables }) => void,
            result: MutationResult<EditTaskMutation>
          ) => (
            <form
              onSubmit={e => {
                e.preventDefault()

                editTask({
                  variables: {
                    projId: props.projectId,
                    taskId: props.task.id,
                    newTask: {
                      name: task.name,
                      points: task.points,
                      dueDate: hasDate
                        ? task.dueDate || addDays(new Date(), 1)
                        : undefined,
                      lastEdited: new Date(),
                      recurrance: task.recurrance,
                      color: task.color
                    }
                  }
                })
              }}
            >
              <DialogTitle>Editing Task</DialogTitle>
              <IconButton className={classes.closeButton} onClick={onClose}>
                <Close />
              </IconButton>
              <DialogContent>
                {task.description && (
                  <DialogContentText>{task.description}</DialogContentText>
                )}
                <div
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end',
                    flex: '0 0 auto'
                  }}
                >
                  <TextField
                    style={{ margin: '0 4px' }}
                    required
                    autoFocus
                    label="Task Name"
                    value={task.name}
                    onChange={({ target }) =>
                      setTask({ ...task, name: target.value })
                    }
                    fullWidth
                  />
                  <TextField
                    style={{ margin: '0 4px', width: '33%' }}
                    required
                    fullWidth
                    label="Point Importance"
                    value={task.points}
                    type="number"
                    onChange={e => {
                      e.persist() // for some reason it unfocuses without this!
                      if (parseInt(e.target.value) >= 0) {
                        setTask({ ...task, points: parseInt(e.target.value) })
                      }
                    }}
                  />
                </div>
                {/* handles whether has date or not, selecting date, and recurrance */}
                <TaskModalDateField
                  task={task}
                  setTask={setTask}
                  setHasDate={setHasDate}
                  hasDate={hasDate}
                />

                <TaskModalComments
                  task={task}
                  setComments={newComments => {
                    setTask({ ...task, comments: newComments })
                  }}
                />
                <TaskModalSubtasks
                  task={task}
                  setTask={newTask => {
                    setTask(newTask)
                  }}
                />
                <div style={{ display: 'flex', marginTop: 8 }}>
                  <ChooseColor
                    color={task.color || '#FFFFFF'}
                    onChange={(color: string) => {
                      setTask({ ...task, color })
                    }}
                  />

                  <div style={{ width: 24 }} />

                  <TaskModalChangeTasksColumnField
                    setColumnId={setColumnId}
                    columnId={columnId}
                    projects={projects}
                  />
                </div>
                <TaskModalTags
                  tags={project.tags}
                  task={task}
                  setTask={newTask => setTask(newTask)}
                  setAnchor={setAnchor}
                />
                <TaskModalManualTimeEntry
                  task={task}
                  extraMinutes={extraMinutes}
                  extraHours={extraHours}
                  setTask={setTask}
                  setExtraHours={setExtraHours}
                  setExtraMinutes={setExtraMinutes}
                />
                {props.projectId !== undefined && (
                  <>
                    <Typography
                      style={{
                        margin: '6px 0px',
                        fontSize: 18
                      }}
                      variant="subtitle1"
                    >
                      Assigned Members
                    </Typography>
                    <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                      {Object.values(props.projects[props.projectId].users).map(
                        user => (
                          <Paper
                            key={user.id}
                            style={{
                              padding: 6,
                              display: 'flex',
                              alignItems: 'center',
                              margin: 8
                            }}
                          >
                            <img
                              src={user.profileImg}
                              style={{
                                borderRadius: '50%',
                                height: 48,
                                width: 48
                              }}
                            />
                            <span style={{ marginLeft: 16, fontSize: 20 }}>
                              {user.username}
                            </span>
                            <IconButton
                              onClick={() => {
                                if (task.assignedUsers.includes(user.id)) {
                                  setTask({
                                    ...task,
                                    assignedUsers: task.assignedUsers.filter(
                                      id => id !== user.id
                                    )
                                  })
                                } else {
                                  setTask({
                                    ...task,
                                    assignedUsers: [
                                      ...task.assignedUsers,
                                      user.id
                                    ]
                                  })
                                }
                              }}
                              style={{ marginLeft: 8 }}
                            >
                              {task.assignedUsers.includes(user.id) ? (
                                <Clear />
                              ) : (
                                <Add />
                              )}
                            </IconButton>
                          </Paper>
                        )
                      )}
                    </div>
                  </>
                )}
              </DialogContent>
              <DialogActions>
                <Button onClick={onClose} color="secondary">
                  Cancel
                </Button>
                <Mutation
                  onCompleted={({ deleteTask }) => {
                    console.log(deleteTask)
                    if (deleteTask && deleteTask.project) {
                      props.setTaskA({
                        id: props.task.id,
                        newTask: null,
                        projectId: props.projectId
                      })
                      props.setProjectA({
                        id: deleteTask.project.id,
                        newProj: deleteTask.project
                      })
                    }
                  }}
                  mutation={GQL_DELETE_TASK}
                >
                  {(
                    deleteTask: (args: {
                      variables: DeleteTaskMutationVariables
                    }) => void,
                    deleteTaskResult: MutationResult<DeleteTaskMutation>
                  ) => {
                    return (
                      <Button
                        onClick={() => {
                          console.log('whoop delete')
                          deleteTask({
                            variables: {
                              projId: props.projectId,
                              taskId: props.task.id
                            }
                          })
                          props.onClose()
                        }}
                      >
                        Delete Task
                        <Delete style={{ marginLeft: 8 }} />
                      </Button>
                    )
                  }}
                </Mutation>
                <Button type="submit" variant="contained" color="primary">
                  Save Changes
                </Button>
              </DialogActions>
            </form>
          )}
        </Mutation>
      </Dialog>
      <CreateTag
        projectId={project.id}
        createTag={(name: string, color: string) =>
          props.setTagA({
            id: props.task.id,
            projectId: props.projectId,
            newTag: { name, color, id: uuid() }
          })
        }
        tags={project.tags}
        open={Boolean(anchor)}
        deleteTag={(id: string) =>
          props.setTagA({ id, projectId: project.id, newTag: null })
        }
        handleClose={() => setAnchor(null)}
        anchorEl={anchor}
        task={task}
      />
    </>
  )
}

const mapState = (state: TState) => ({
  projects: state.projects
})

const actionCreators = {
  setTaskA,
  setTagA,
  setProjectA: setProjectA
}

export const TaskModal = connect(
  mapState,
  actionCreators
)(withStyles(styles)(CTaskModal))
