import { resToNiceProject } from './../../API/utils'
import { TProjects } from '../../types/project'
import { defaultState } from '../defaultState'
import { createReducer } from './createReducer'
import { TState } from '../../types/state'
import {
  setProjectA,
  setSwimlaneA,
  selectMemberA,
  setProjectsA
} from '../actions/project'
import { setColumnA } from '../actions/column'
import { ReducerCases } from '../actions/types'
import { taskCases } from './tasks'
import { resToNiceProjects } from '../../API/utils'
import { tagsCases } from './tags'
import { TagAction } from '../actions/tags'

const SET_PROJECT = (
  projects: TProjects,
  action: ReturnType<typeof setProjectA>
) => {
  if (projects[action.id] && action.project !== null) {
    projects[action.id] = Object.assign(
      projects[action.id],
      resToNiceProject(action.project)
    )
  } else if (action.project) {
    projects[action.id] = resToNiceProject(action.project)
  } else {
    delete projects[action.id]
  }
}

const SET_COLUMN = (
  projects: TProjects,
  action: ReturnType<typeof setColumnA>
) => {
  const changing = projects[action.projectId].columns

  if (action.newColumn) {
    if (changing[action.id] === undefined) {
      projects[action.projectId].columnOrder.push(action.id)
    }
    changing[action.id] = {
      ...changing[action.id],
      ...action.newColumn
    } as any
  } else {
    projects[action.projectId].columnOrder = projects[
      action.projectId
    ].columnOrder.filter(id => id !== action.id)

    delete changing[action.id]
  }
}

const SET_SWIMLANE = (
  projects: TProjects,
  action: ReturnType<typeof setSwimlaneA>
) => {
  const changing = projects[action.projectId].swimlanes

  if (action.swimlane) {
    changing[action.id] = {
      ...changing[action.id],
      ...action.swimlane,
      id: action.id
    }
  } else {
    projects[action.projectId].swimlaneOrder.splice(
      projects[action.projectId].swimlaneOrder.indexOf(action.id),
      1
    )

    delete changing[action.id]
  }
}

const SELECT_MEMBER = (
  projects: TProjects,
  action: ReturnType<typeof selectMemberA>
): TProjects => {
  let selectedMember = projects[action.projectId].selectingMember

  if (selectedMember === action.id) {
    selectedMember = null
  } else {
    selectedMember = action.id
  }

  return {
    ...projects,
    [action.projectId]: {
      ...projects[action.projectId],
      selectingMember: selectedMember
    }
  }
}

// just give those reducers the tags cause thats what they only need
const tagReducers: any = {}
Object.keys(tagsCases).map(key => {
  tagReducers[key] = (projects: TProjects, action: TagAction) => {
    const reducer = (tagsCases as any)[key]

    return reducer(projects[action.projectId].tags, action)
  }
})

const projectCases: ReducerCases<TProjects> = {
  SET_PROJECT,
  SET_COLUMN,
  SET_SWIMLANE,
  SELECT_MEMBER,
  SET_PROJECTS: (projects, action: ReturnType<typeof setProjectsA>) => {
    return resToNiceProjects(action.projects)
  }
}

export const projectsReducer = createReducer<TState['projects']>(
  defaultState.projects,
  {
    ...projectCases,
    ...taskCases,
    ...tagReducers
  } as ReducerCases<TState['projects']>
)
