import React from 'react'
import { TTask } from '../../types/task'
import {
  Typography,
  IconButton,
  TextField,
  Checkbox,
  Button
} from '@material-ui/core'
import { Delete, Add } from '@material-ui/icons'
import uuid from 'uuid'
type TProps = {
  task: TTask
  setTask: (newTask: TTask) => void
}

export const TaskModalSubtasks = (props: TProps) => {
  const { task, setTask } = props

  return (
    <>
      {task.subTasks.length > 0 && (
        <Typography
          style={{
            margin: '10px 0px',
            fontSize: 18
          }}
          variant="subtitle1"
        >
          Subtasks
        </Typography>
      )}
      {task.subTasks.map((subTask, i) => (
        <div key={subTask.id} style={{ display: 'flex' }}>
          <IconButton
            style={{ margin: 10 }}
            onClick={() => {
              const without = [
                ...task.subTasks.slice(0, i),
                ...task.subTasks.slice(i + 1)
              ]
              setTask({ ...task, subTasks: without })
            }}
          >
            <Delete />
          </IconButton>
          <TextField
            key={subTask.id}
            margin="dense"
            required
            fullWidth
            label={`Subtask ${i}`}
            value={task.subTasks[i].name}
            onChange={e => {
              setTask({
                ...task,
                subTasks: [
                  ...task.subTasks.slice(0, i),
                  { ...task.subTasks[i], name: e.target.value },
                  ...task.subTasks.slice(i + 1)
                ]
              })
            }}
          />
          <Checkbox
            style={{ marginTop: 12 }}
            disableRipple
            checked={task.subTasks[i].completed}
            onChange={e => {
              const subTasks = [...task.subTasks]
              subTasks[i].completed = !subTasks[i].completed
              setTask({ ...task, subTasks })
            }}
          />
        </div>
      ))}
      <Button
        color="secondary"
        style={{
          marginTop: 8,
          marginBottom: 8,
          marginLeft: 'auto'
        }}
        onClick={() => {
          setTask({
            ...task,
            subTasks: [
              ...task.subTasks,
              { completed: false, name: 'Subtask Name', id: uuid() }
            ]
          })
        }}
      >
        <Add style={{ marginRight: 8 }} />
        Create Subtask
      </Button>
    </>
  )
}
