import React, { Dispatch, SetStateAction } from 'react'
import {
  IconButton,
  Menu,
  MenuItem,
  ListItemIcon,
  DialogTitle,
  DialogContent,
  Dialog,
  DialogActions,
  Button,
  TextField,
  Theme,
  createStyles,
  WithStyles,
  withStyles
} from '@material-ui/core'
import {
  ArrowBack,
  DeleteForever,
  Add,
  ArrowForward,
  MoreHoriz
} from '@material-ui/icons'
import { setColumnA } from '../../store/actions/column'
import { TColumn } from '../../types/state'
import { Mutation, MutationResult } from 'react-apollo'
import { GQL_EDIT_COLUMN } from '../../graphql/mutations/column'
import {
  EditColumnMutationVariables,
  EditColumnMutation
} from '../../graphql/types'

type OwnProps = {
  moveColumn: () => void
  setDeleting: Dispatch<SetStateAction<boolean>>
  setCreatingTask: Dispatch<SetStateAction<boolean>> // should be removed later for a floating action button below each column :)
  projectId: string
  column: TColumn
  collapse: () => void
  index: number | 'end'
  setColumn: typeof setColumnA
  setWIP: (newWIP: string) => void
  disableAdd: boolean
} & WithStyles<typeof styles>

/** @todo set name to be an input when hovering/clicking */

const styles = (theme: Theme) =>
  createStyles({
    input: {
      marginTop: 'auto',
      marginBottom: 'auto',
      overflow: 'hidden',
      whiteSpace: 'nowrap',
      textOverflow: 'ellipsis',
      fontSize: 18,
      outline: 'none',
      backgroundColor: '#f5f5f5',
      borderRadius: 4,
      padding: 4,
      marginLeft: 5,
      width: '100%',
      border: '1px solid transparent',
      '&:hover': {
        backgroundColor: 'white'
      },
      '&:focus': {
        borderColor: '#27b6ba'
      }
    },
    root: {
      backgroundColor: '#f2f2f2',
      padding: '4px 0px',
      display: 'flex',
      flexDirection: 'row'
    },
    lengthMarker: {
      margin: 'auto 4px',
      fontSize: '1rem',
      color: 'rgba(0,0,0,0.56)'
    }
  })

export const ColumnHeader = withStyles(styles)((props: OwnProps) => {
  const [anchorEl, setAnchorEl] = React.useState(null as React.ReactNode)
  const [editingWIPLimit, setEditingWIPLimit] = React.useState(false)
  const [wipLimit, setWipLimit] = React.useState(
    props.column.taskLimit ? props.column.taskLimit.toString() : '3'
  )
  const { setDeleting, column, collapse, setCreatingTask, classes } = props
  return (
    <>
      <div className={classes.root}>
        <IconButton
          style={{ marginLeft: 5 }}
          disabled={props.disableAdd}
          onClick={() => setCreatingTask(true)}
          color="primary"
        >
          <Add />
        </IconButton>
        <input
          className={classes.input}
          value={column.name}
          onChange={e => {
            props.setColumn({
              id: column.id,
              projectId: props.projectId,
              newCol: { name: e.target.value }
            })
          }}
        />
        <span className={classes.lengthMarker}>
          ({column.taskIds.length}
          {column.taskLimit && '/' + column.taskLimit})
        </span>
        <IconButton
          style={{ marginLeft: 'auto', marginRight: 5 }}
          onClick={e => setAnchorEl(e.currentTarget)}
        >
          <MoreHoriz />
        </IconButton>
      </div>
      <Menu
        anchorEl={anchorEl as any}
        open={Boolean(anchorEl)}
        style={{ minWidth: 200 }}
        onClose={() => setAnchorEl(null)}
      >
        <MenuItem
          onClick={() => {
            setAnchorEl(null)
            props.moveColumn()
            // moveColumn(projectId, column.id, 'Back')
          }}
        >
          <ListItemIcon>
            <ArrowBack />
          </ListItemIcon>
          Move to Left
        </MenuItem>
        <MenuItem
          onClick={() => {
            setAnchorEl(null)
            props.moveColumn()
            // moveColumn(projectId, column.id, 'Forward')
          }}
        >
          <ListItemIcon>
            <ArrowForward />
          </ListItemIcon>
          Move to Right
        </MenuItem>
        <MenuItem
          onClick={() => {
            setAnchorEl(null)
            setDeleting(true)
          }}
        >
          <ListItemIcon>
            <DeleteForever />
          </ListItemIcon>
          Delete
        </MenuItem>
        <MenuItem
          onClick={() => {
            setEditingWIPLimit(true)
            setAnchorEl(null)
          }}
        >
          Set WIP Limit
        </MenuItem>
        <MenuItem
          onClick={() => {
            collapse()
          }}
        >
          Collapse Column
        </MenuItem>
      </Menu>
      <Dialog onClose={() => setEditingWIPLimit(false)} open={editingWIPLimit}>
        <Mutation mutation={GQL_EDIT_COLUMN}>
          {(
            setLimit: (args: {
              variables: EditColumnMutationVariables
            }) => void,
            result: MutationResult<EditColumnMutation>
          ) => {
            return (
              <div style={{ minWidth: 400 }}>
                <DialogTitle>Set {column.name} WIP Limit</DialogTitle>
                <DialogContent>
                  <TextField
                    type="number"
                    fullWidth
                    value={wipLimit}
                    onChange={e => setWipLimit(e.target.value)}
                  />
                </DialogContent>
                <DialogActions>
                  <Button
                    fullWidth
                    color="secondary"
                    onClick={() => setEditingWIPLimit(false)}
                  >
                    Cancel
                  </Button>
                  <Button
                    onClick={() => {
                      props.setWIP(wipLimit)
                      setEditingWIPLimit(false)
                    }}
                    fullWidth
                    color="primary"
                    variant="contained"
                  >
                    Submit
                  </Button>
                </DialogActions>
              </div>
            )
          }}
        </Mutation>
      </Dialog>
    </>
  )
})
