import React, { CSSProperties, useState } from 'react'
import { render } from 'react-dom'
import './index.css'
import { Provider } from 'react-redux'
import { MuiThemeProvider } from '@material-ui/core/styles'
import { Switch, Route, HashRouter } from 'react-router-dom'

import { store } from './store/store'
import { createMuiTheme } from '@material-ui/core/styles'
import { MuiPickersUtilsProvider } from 'material-ui-pickers'
import DateFnsUtils from '@date-io/date-fns'
import { Pomodoro } from './components/Pomodoro/Pomodoro'
import { AuthRender } from './components/Auth/Auth'
import { CreateProject } from './components/createProject/CreateProject'
import { Project } from './components/Project/Project'
import { NoMatch } from './components/NoMatch/NoMatch'
import { HomePage } from './components/Home/Home'
import { Fab, CircularProgress } from '@material-ui/core'
import { Timer } from '@material-ui/icons'
import { Week } from './components/Calendar/Week'
import { About } from './components/About/About'
import { TaskList } from './components/TaskList/TaskList'

import 'react-typist/dist/Typist.css'
import { Header } from './components/Header'
import { Settings } from './components/Project/Settings/Settings'
import { Goals } from './components/Goals/Goals'
import { WelcomeDialog } from './components/Welcome/WelcomeDialog'
import { SnackbarRoot } from './components/utils/SnackbarRoot'
import { ApolloProvider } from 'react-apollo'
import { print } from 'graphql'
import { loginA } from './store/actions/auth'
import { Mutation } from './graphql/types'
import { fetchQuery } from './API/initialize'

// const primary = '#0336FF'
// const secondary = '#00838f'
const primary = '#f4511e'
const secondary = '#3f51b5'

/** @description Material ui theme, used in wrapper.tsx */

const theme = createMuiTheme({
  palette: {
    primary: {
      main: primary
    },
    secondary: {
      main: secondary
    }
  }
})

export const fabStyle: CSSProperties = {
  position: 'fixed',
  bottom: theme.spacing(2),
  left: theme.spacing(2),
  zIndex: 999
}

import { ApolloClient } from 'apollo-client'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { HttpLink } from 'apollo-link-http'
import { onError } from 'apollo-link-error'
import { ApolloLink } from 'apollo-link'
import { openSnackbarA } from './store/actions/snackbar'
import { GQL_LOGIN_WITH_COOKIE } from './graphql/mutations/auth'

const client = new ApolloClient({
  connectToDevTools: true,
  link: ApolloLink.from([
    onError(({ graphQLErrors, networkError }) => {
      if (graphQLErrors) {
        graphQLErrors.map(({ message, locations, path }) =>
          console.error(
            `GQL ERROR: Message: ${message}, Location: ${locations}, Path: ${path}`
          )
        )
      }
      if (networkError) {
        console.error(`[Network error]: ${networkError}`)
      }
    }),
    new HttpLink({
      uri: 'http://localhost:4000/graphql',
      credentials: 'include'
    })
  ]),
  cache: new InMemoryCache()
})

const Router = () => {
  const [open, setOpen] = useState(false)
  const [loaded, setLoaded] = useState(false)

  window.onload = async () => {
    try {
      const {
        loginWithCookie
      }: { loginWithCookie: Mutation['loginWithCookie'] } = await fetchQuery(
        print(GQL_LOGIN_WITH_COOKIE)
      )

      if (loginWithCookie && loginWithCookie.user) {
        store.dispatch(loginA(loginWithCookie.user as any) as any)
      } else {
        store.dispatch(openSnackbarA('Hey there, Welcome!', 'standard'))
      }

      setLoaded(true)
    } catch (err) {
      setLoaded(true)
    }
  }

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <HashRouter>
        <>
          <Header />
          <Pomodoro open={open} stateFunc={(bool: boolean) => setOpen(bool)} />
          <WelcomeDialog />
          {!open && (
            <>
              <Fab
                style={fabStyle}
                color="secondary"
                onClick={() => setOpen(true)}
              >
                <Timer />
              </Fab>
            </>
          )}
          {loaded ? (
            <Switch>
              <Route exact path="/dashboard" component={HomePage} />
              <Route
                exact
                path="/login"
                render={() => <AuthRender authType="Login" />}
              />
              <Route
                exact
                path="/register"
                render={() => <AuthRender authType="Register" />}
              />
              <Route
                exact
                path="/create-project"
                render={() => <CreateProject />}
              />
              <Route
                path="/project/:id"
                render={props => {
                  return <Project id={props.match.params.id} />
                }}
              />
              <Route exact path="/week" component={Week} />
              <Route exact path="/" component={About} />
              <Route exact path="/tasks" component={TaskList} />
              <Route exact path="/settings" render={() => <Settings />} />
              <Route exact path="/goals" component={Goals} />
              <Route component={NoMatch} />
            </Switch>
          ) : (
            <div
              style={{
                position: 'absolute',
                top: 0,
                bottom: 0,
                left: 0,
                right: 0,
                display: 'flex',
                justifyContent: 'space-around'
              }}
            >
              <CircularProgress
                style={{
                  width: '128px',
                  height: '128px',
                  marginRight: 64,
                  marginTop: 100
                }}
              />
            </div>
          )}
        </>
      </HashRouter>
    </MuiPickersUtilsProvider>
  )
}

export const Wrapper = () => {
  return (
    <Provider store={store}>
      <ApolloProvider client={client}>
        <MuiThemeProvider theme={theme}>
          <SnackbarRoot />
          <Router />
        </MuiThemeProvider>
      </ApolloProvider>
    </Provider>
  )
}

render(<Wrapper />, document.getElementById('root'))
