import React, { useState } from 'react'
import { connect } from 'react-redux'
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
  Button,
  Theme,
  createStyles,
  WithStyles,
  IconButton,
  withStyles,
  Checkbox,
  Tooltip
} from '@material-ui/core'
import {
  Close,
  KeyboardArrowLeft,
  KeyboardArrowRight
} from '@material-ui/icons'
import { Change } from '../../types/types'
import { setTaskA } from '../../store/actions/task'
import { ChooseColor } from '../utils/chooseColor'
import { DateTimePicker } from 'material-ui-pickers'
import { TState } from '../../types/state'
import { Mutation, MutationResult } from 'react-apollo'
import {
  CreateTaskMutation,
  CreateTaskMutationVariables
} from '../../graphql/types'
import { GQL_CREATE_TASK } from '../../graphql/mutations/task'
import { setProjectA } from '../../store/actions/project'

type CreateTaskProps = ReturnType<typeof mapState> &
  typeof actionCreators &
  OwnProps &
  WithStyles<typeof styles>

interface OwnProps {
  projectId: string
  columnId: string
  onClose(): void
}

const styles = (theme: Theme) =>
  createStyles({
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500]
    }
  })

const CCreateTask = (props: CreateTaskProps) => {
  const [name, setName] = useState('')
  const [color, setColor] = useState('#FFFFFF')
  const [dueDate, setDueDate] = useState(new Date())
  const [hasDate, setHasDate] = useState(false)

  const { onClose, classes } = props
  return (
    <Dialog open={true} onClose={onClose}>
      <Mutation
        mutation={GQL_CREATE_TASK}
        onCompleted={({ createTask }) => {
          if (createTask && createTask.task && createTask.project) {
            props.setTask({
              newTask: createTask.task,
              id: createTask.task.id,
              projectId: createTask.project.id
            })
            props.setProject({
              id: createTask.project.id,
              newProj: createTask.project
            })
          }
          props.onClose()
        }}
        onError={() => props.onClose()}
      >
        {(
          createTask: (obj: { variables: CreateTaskMutationVariables }) => any,
          result: MutationResult<CreateTaskMutation>
        ) => {
          return (
            <form
              onSubmit={e => {
                e.preventDefault()

                createTask({
                  variables: {
                    projId: props.projectId,
                    columnId: props.columnId,
                    color,
                    name
                  }
                })
              }}
            >
              <DialogTitle>Create Task</DialogTitle>
              <IconButton className={classes.closeButton} onClick={onClose}>
                <Close />
              </IconButton>
              <DialogContent>
                <DialogContentText>
                  Tasks should be the smallest possible chunk of what you need
                  to do. For example, if you needed to create a Youtube
                  thumbnail, the first task would be: "Create Title".
                </DialogContentText>
                <TextField
                  autoFocus
                  margin="dense"
                  label="Task Name"
                  value={name}
                  onChange={({ target }: Change) => setName(target.value)}
                  fullWidth
                />
                <div style={{ display: 'flex', margin: '16px 0px' }}>
                  <DateTimePicker
                    fullWidth
                    showTabs={false}
                    leftArrowIcon={<KeyboardArrowLeft />}
                    rightArrowIcon={<KeyboardArrowRight />}
                    value={dueDate}
                    disablePast
                    onChange={(date: Date) => setDueDate(date)}
                    label="Due Date"
                    showTodayButton
                  />
                  <Tooltip title="Should Task enforce Due Date?">
                    <Checkbox
                      style={{ display: 'inline-block' }}
                      checked={hasDate}
                      onChange={e => setHasDate(e.target.checked)}
                    />
                  </Tooltip>
                </div>
                <ChooseColor
                  color={color}
                  onChange={(newColor: string) => setColor(newColor)}
                />
              </DialogContent>
              <DialogActions>
                <Button onClick={onClose} color="secondary">
                  Cancel
                </Button>
                <Button type="submit" variant="contained" color="primary">
                  Create Task
                </Button>
              </DialogActions>
            </form>
          )
        }}
      </Mutation>
    </Dialog>
  )
}

const mapState = (state: TState, ownProps: OwnProps) => {
  return {
    column: state.projects[ownProps.projectId].columns[ownProps.columnId]
  }
}
const actionCreators = {
  setTask: setTaskA,
  setProject: setProjectA
}

export const CreateTask = connect(
  mapState,
  actionCreators
)(withStyles(styles)(CCreateTask))
