import React from 'react'
import { IconButton, TextField, Typography, Fab } from '@material-ui/core'
import { Delete, Add } from '@material-ui/icons'
import { formatDueDate } from '../../utils/formatDueDate'
import { TTask } from '../../types/task'
import uuid from 'uuid'

type TProps = {
  task: TTask
  setComments: (newComments: TTask['comments']) => void
}

const AddFab = (props: { onClick: () => void }) => (
  <Fab
    style={{
      marginTop: 'auto',
      marginBottom: 16,
      marginLeft: 8,
      height: 40,
      width: 40,
      boxShadow: 'none'
    }}
    color="primary"
    onClick={props.onClick}
  >
    <Add />
  </Fab>
)

export const TaskModalComments = (props: TProps) => {
  const { task } = props

  const addClick = () => {
    props.setComments([
      ...task.comments,
      { description: '', dateAdded: new Date(), id: uuid() }
    ])
  }

  return (
    <>
      {task.comments.length > 0 && (
        <Typography
          style={{
            margin: '10px 0px',
            fontSize: 18
          }}
          variant="subtitle1"
        >
          Comments
        </Typography>
      )}
      {task.comments.map((comment, i) => (
        <div key={comment.id} style={{ display: 'flex' }}>
          <IconButton
            style={{ margin: 10 }}
            onClick={() => {
              const without = [
                ...task.comments.slice(0, i),
                ...task.comments.slice(i + 1)
              ]
              props.setComments(without)
            }}
          >
            <Delete />
          </IconButton>
          <TextField
            key={comment.id}
            margin="dense"
            required
            style={{ flexGrow: 1 }}
            value={task.comments[i].description}
            label={formatDueDate({
              ...task,
              dueDate: comment.dateAdded,
              recurrance: undefined
            })}
            onChange={e => {
              const newComments = [...task.comments]
              newComments[i].description = e.target.value
              newComments[i].lastEdited = new Date()
              props.setComments(newComments)
            }}
          />
          {i === task.comments.length - 1 && <AddFab onClick={addClick} />}
        </div>
      ))}
      {task.comments.length === 0 && (
        <div style={{ display: 'flex', marginTop: 8 }}>
          <AddFab onClick={addClick} />
          <Typography
            variant="subtitle1"
            style={{ marginLeft: 12, marginTop: 6, fontWeight: 400 }}
          >
            Add Comment
          </Typography>
        </div>
      )}
    </>
  )
}
