/**
 * This will display all of the users task in their own row, it will be expandable to show more information,
 * while also being able to open up a modal(will need to do some event propagation stuff like how we did in the base task)
 * They can order them by default(most recently added, just Object.values(tasks)) or due Date etc.
 * They can filter by color and search tasks
 */

import React from 'react'
import { connect } from 'react-redux'
import { TTask } from '../../types/task'
import { TState } from '../../types/state'
import {
  Theme,
  createStyles,
  withStyles,
  WithStyles,
  Typography,
  Paper
} from '@material-ui/core'
import { BaseTask } from '../Task/Base'
import { TaskModal } from '../TaskModal/TaskModal'
import { TaskSearch } from './TaskSearch'
import Helmet from 'react-helmet'
import {
  getTasksProject,
  getAllTasksArr,
  getProjectIdFromTaskId
} from '../../utils/utilities'

type TProps = ReturnType<typeof mapState> &
  typeof actionCreators &
  WithStyles<typeof styles>

const styles = (theme: Theme) =>
  createStyles({
    root: {
      width: '100%'
    }
  })

const CTaskList = (props: TProps) => {
  const [openTask, setOpenTask] = React.useState(null as TTask | null)
  const [ids, setIds] = React.useState(Object.keys(props.tasks))
  const { classes } = props

  const tasks = [
    ...Object.values(props.tasks).filter(task => ids.includes(task.id))
  ]

  return (
    <div className={classes.root}>
      <Helmet>
        <style type="text/css">{` body { background-color: #1d364c; }`}</style>
      </Helmet>

      <Paper style={{ margin: 40, padding: 20 }}>
        <Typography align="center" style={{ marginBottom: 10 }} variant="h4">
          {Object.keys(props.tasks).length} Tasks
        </Typography>
        <TaskSearch setIds={setIds} tasks={Object.values(props.tasks)} />
        {tasks.length ? (
          tasks.map(task => (
            <div style={{ cursor: 'pointer' }} key={task.id}>
              <BaseTask
                project={
                  props.projects[
                    getProjectIdFromTaskId(props.projects, task.id)
                  ]
                }
                projectName={task.projectName}
                task={task}
                bulky
                openFunc={() => setOpenTask(task)}
                provided={
                  {
                    draggableProps: { style: {} },
                    dragHandleProps: {}
                  } as any
                }
                snapshot={{ isDragging: false } as any}
              />
            </div>
          ))
        ) : (
          <Typography align="center" variant="h5">
            No Matching Tasks ಡ_ಡ
          </Typography>
        )}
      </Paper>
      {openTask && (
        <TaskModal
          projectId={getProjectIdFromTaskId(props.projects, openTask.id)}
          task={openTask}
          onClose={() => setOpenTask(null)}
        />
      )}
    </div>
  )
}

const taskWithProjectName = (
  state: TState,
  task: TTask
): TTask & { projectName?: string } => ({
  ...task,
  projectName: (getTasksProject(task.id, state.projects) || { name: undefined })
    .name
})

type NewTasks = { [id: string]: TTask & { projectName?: string } }
const tasksWith = (state: TState): NewTasks => {
  let result: NewTasks = {}

  getAllTasksArr(state.projects).map(task => {
    result = { ...result, [task.id]: taskWithProjectName(state, task) }
  })

  return result
}

const mapState = (state: TState) => ({
  tasks: tasksWith(state),
  projects: state.projects
})

const actionCreators = {}

export const TaskList = withStyles(styles)(
  connect(
    mapState,
    actionCreators
  )(CTaskList)
)
