import React, { useState } from 'react'
import { TextField } from '@material-ui/core'
import { formalize } from '../../utils/utilities'
import { TTask } from '../../types/task'

type TProps = {
  tasks: TTask[]
  setIds: (newIds: string[]) => void
}

export const TaskSearch = (props: TProps) => {
  const [value, setValue] = useState('')

  const type = (newText: string) => {
    setValue(newText)
    if (formalize(newText) === '') {
      props.setIds(props.tasks.map(task => task.id)) // all tasks
    } else {
      const filterText = newText
        .toLowerCase()
        .split(' ') // search by each word, not the thing as a whole
        .filter(subStr => subStr.length) // no trailing spaces dog

      props.setIds(
        props.tasks
          .filter(task => {
            let taskText = task.name + task.points.toString()

            taskText += task.comments.reduce(
              (str, com) => (str += com.description),
              ''
            )
            taskText += task.subTasks.reduce((str, sub) => str + sub.name, '')
            taskText += task.tags.reduce((str, tag) => (str += tag), '')

            taskText = formalize(taskText)

            // only check if ONE subStr is included in a task so that more results show up
            for (const subStr of filterText) {
              if (taskText.includes(subStr)) {
                return true
              }
            }
            return false
          })
          .map(task => task.id)
      )
    }
  }

  return (
    <div>
      <TextField
        fullWidth
        variant="outlined"
        label="Search"
        placeholder="Name, Comment, SubTask, Points"
        value={value}
        style={{ marginBottom: 8 }}
        onChange={e => type(e.target.value)}
      />
    </div>
  )
}
