import React, { SetStateAction, Dispatch } from 'react'
import { DateTimePicker } from 'material-ui-pickers'
import {
  KeyboardArrowLeft,
  KeyboardArrowRight,
  Delete
} from '@material-ui/icons'
import {
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  IconButton,
  Button,
  Typography
} from '@material-ui/core'
import { TTask } from '../../types/task'
import { isAfter } from 'date-fns'
import { isBefore } from 'date-fns/esm'

type TProps = {
  hasDate: boolean
  setHasDate: Dispatch<SetStateAction<boolean>>
  setTask: Dispatch<SetStateAction<TTask>>
  task: TTask
}

export const TaskModalDateField = ({
  hasDate,
  setTask,
  task,
  setHasDate
}: TProps) => {
  return (
    <div
      style={{
        marginTop: 10,
        display: 'flex',
        justifyContent: 'flex-end',
        flex: '0 0 auto'
      }}
    >
      {hasDate ? (
        <div style={{ flexGrow: 1 }}>
          <Typography
            style={{
              display: 'block',
              margin: '6px 0px',
              fontSize: 18
            }}
            variant="subtitle1"
          >
            Due Date
          </Typography>
          <div
            style={{
              display: 'flex'
            }}
          >
            <DateTimePicker
              fullWidth
              showTabs={false}
              emptyLabel="No Start Time"
              leftArrowIcon={<KeyboardArrowLeft />}
              rightArrowIcon={<KeyboardArrowRight />}
              value={task.startDate || null}
              onChange={(newDate: Date) => {
                if (task.dueDate && isAfter(newDate, task.dueDate)) {
                } else if (!task.recurrance) {
                  setTask({ ...task, startDate: newDate })
                }
              }}
              maxDate={task.dueDate || undefined}
              label="Starting Date"
              showTodayButton
            />
            <DateTimePicker
              style={{ marginLeft: 8 }}
              fullWidth
              showTabs={false}
              leftArrowIcon={<KeyboardArrowLeft />}
              rightArrowIcon={<KeyboardArrowRight />}
              value={task.dueDate || new Date()}
              minDate={task.startDate || undefined}
              onChange={(newDate: Date) => {
                if (task.startDate && isBefore(newDate, task.startDate)) {
                } else {
                  setTask({ ...task, dueDate: newDate })
                }
              }}
              label="Due Date"
              showTodayButton
            />
            <FormControl style={{ marginLeft: 8, width: '100%' }}>
              <InputLabel>Recurrance</InputLabel>
              <Select
                value={task.recurrance || 'NO'}
                onChange={e => {
                  if (!task.startDate) {
                    setTask({
                      ...task,
                      recurrance:
                        e.target.value === 'NO'
                          ? undefined
                          : (e.target.value as undefined | 'daily' | 'weekly')
                    })
                  }
                }}
              >
                <MenuItem value={'NO'}>None</MenuItem>
                <MenuItem value={'daily'}>Daily</MenuItem>
                <MenuItem value={'weekly'}>Weekly</MenuItem>
              </Select>
            </FormControl>
            <IconButton
              style={{ display: 'inline-block', marginTop: 'auto' }}
              onClick={e => setHasDate(false)}
            >
              <Delete />
            </IconButton>
          </div>
        </div>
      ) : (
        <>
          <Button
            fullWidth
            variant="outlined"
            color="secondary"
            style={{ display: 'inline-block' }}
            onClick={e => setHasDate(true)}
          >
            Add Due Date
          </Button>
        </>
      )}
    </div>
  )
}
