import { TGoals } from '../../types/state'
import { TSetGoal } from './../actions/goals'
import { defaultState } from '../defaultState'
import { createReducer } from './createReducer'
import { TState } from '../../types/state'

/**
 * @reminder don't forget to refund purchased items when deleting them from non-inventory!
 */

const SET_GOAL = (goals: TGoals, action: TSetGoal) => {
  if (goals[action.id]) {
    goals[action.id] = Object.assign(goals[action.id], action.goal)
  }
}

export const goalsReducer = createReducer<TState['goals']>(defaultState.goals, {
  SET_GOAL
})
