import React, { Dispatch, SetStateAction } from 'react'
import {
  FormControl,
  Select,
  MenuItem,
  FormHelperText
} from '@material-ui/core'
import { TProject, TProjects } from '../../types/project'
import { getAllColumnsArr } from '../../utils/utilities'
type TProps = {
  setColumnId: Dispatch<SetStateAction<string>>
  columnId: string | undefined
  projects: TProjects
}

export const TaskModalChangeTasksColumnField = ({
  setColumnId,
  columnId,
  projects
}: TProps) => {
  return (
    <FormControl fullWidth>
      <Select
        fullWidth
        value={columnId}
        onChange={e => {
          setColumnId(e.target.value)
        }}
      >
        <MenuItem value={undefined}>Unassigned</MenuItem>
        {getAllColumnsArr(projects).map((column, i) => {
          const projectName = (Object.values(projects).find(
            project => project.columnOrder.indexOf(column.id) !== -1
          ) as TProject).name
          return (
            <MenuItem key={column.id} value={column.id}>
              <pre>
                <em>{column.name}</em> of {projectName}
              </pre>
            </MenuItem>
          )
        })}
      </Select>
      <FormHelperText>Task's Column</FormHelperText>
    </FormControl>
  )
}
