import gql from 'graphql-tag'
import { projectFields } from '../fragments'

export const GQL_CREATE_COLUMN = gql`
  ${projectFields}

  mutation createColumn(
    $name: String!
    $projId: String!
    $isCompletedColumn: Boolean
    $taskLimit: Int
  ) {
    createColumn(
      projId: $projId
      name: $name
      isCompletedColumn: $isCompletedColumn
      taskLimit: $taskLimit
    ) {
      project {
        ...projectFields
      }
      column {
        ...columnFields
      }
    }
  }
`

export const GQL_DELETE_COLUMN = gql`
  ${projectFields}

  mutation deleteColumn($projectId: String!, $id: String!) {
    deleteColumn(projectId: $projectId, _id: $id) {
      project {
        ...projectFields
      }
    }
  }
`

export const GQL_EDIT_COLUMN = gql`
  mutation editColumn(
    $id: String!
    $projectId: String!
    $wipLimit: Int
    $name: String
    $taskIds: [String!]
    $isCompletedColumn: Boolean
  ) {
    editColumn(
      colId: $id
      projectId: $projectId
      newCol: {
        taskLimit: $wipLimit
        name: $name
        taskIds: $taskIds
        isCompletedColumn: $isCompletedColumn
      }
    ) {
      column {
        name
      }
    }
  }
`
