import { setTagA } from './../actions/tags'
import { ReducerCases } from '../actions/types'
import { TTags } from '../../types/state'

const SET_TAG = (tags: TTags, action: ReturnType<typeof setTagA>) => {
  if (action.newTag === null) {
    delete tags[action.id]
  } else {
    tags[action.id] = {
      ...tags[action.id],
      ...action.newTag
    }
  }
}

export const tagsCases: ReducerCases<TTags> = {
  SET_TAG
}
