import gql from 'graphql-tag'

import { projectFields } from '../fragments'

export const GQL_CREATE_TASK = gql`
  ${projectFields}

  mutation createTask(
    $name: String!
    $color: String!
    $projId: String!
    $dueDate: Date
    $columnId: String!
  ) {
    createTask(
      taskInfo: { name: $name, color: $color, dueDate: $dueDate }
      projId: $projId
      columnId: $columnId
    ) {
      project {
        ...projectFields
      }

      task {
        ...taskFields
      }
    }
  }
`

export const GQL_EDIT_TASK = gql`
  ${projectFields}

  mutation editTask($projId: String!, $taskId: String!, $newTask: TaskInput!) {
    editTask(projId: $projId, taskId: $taskId, task: $newTask) {
      project {
        ...projectFields
      }
      task {
        ...taskFields
      }
    }
  }
`

export const GQL_DELETE_TASK = gql`
  ${projectFields}

  mutation deleteTask($projId: String!, $taskId: String!) {
    deleteTask(projId: $projId, id: $taskId) {
      project {
        ...projectFields
      }
    }
  }
`
