import React from 'react'
import {
  Dialog,
  DialogTitle,
  DialogContent,
  Button,
  DialogActions,
  TextField,
  Typography,
  IconButton,
  ListItem,
  ListItemText,
  ListItemAvatar
} from '@material-ui/core'
import { connect } from 'react-redux'
import { TProject } from '../../types/project'
import { setProjectA, setSwimlaneA } from '../../store/actions/project'
import { Delete, Add } from '@material-ui/icons'
import { Mutation, MutationResult } from 'react-apollo'
import {
  DeleteProjectMutation,
  DeleteProjectMutationVariables
} from '../../graphql/types'
import { GQL_DELETE_PROJECT } from '../../graphql/mutations/project'

type TProps = {
  onClose: () => void
  project: TProject
} & typeof actionCreators

const CProjectSettings = (props: TProps) => {
  const [hasClicked, setClicked] = React.useState(false)

  return (
    <Dialog onClose={props.onClose} open={true}>
      <div style={{ minWidth: '500px' }} />
      <DialogTitle>Project Settings</DialogTitle>
      <DialogContent>
        <Typography style={{ fontSize: 20 }}>
          <span style={{ color: 'red', marginRight: 8 }}>Danger Zone!</span>
        </Typography>
        <ListItem>
          <ListItemAvatar>
            <Delete />
          </ListItemAvatar>
          <ListItemText
            primary="Delete this project"
            secondary="Once deleted, projects cannot be restored"
          />
          <Mutation
            onCompleted={data => {
              if (data.deleteProject && data.deleteProject.id) {
                props.setProject({ id: data.deleteProject.id, newProj: null })
              }
            }}
            mutation={GQL_DELETE_PROJECT}
          >
            {(
              deleteProject: (args: {
                variables: DeleteProjectMutationVariables
              }) => any,
              result: MutationResult<DeleteProjectMutation>
            ) => (
              <Button
                size="medium"
                onClick={() => {
                  if (hasClicked) {
                    deleteProject({ variables: { id: props.project.id } })
                    console.log(result.loading)
                  } else {
                    setClicked(true)
                  }
                }}
                color="primary"
                variant={hasClicked ? 'contained' : 'outlined'}
                style={{ marginLeft: 16, maxHeight: 36, marginTop: 'auto' }}
              >
                {hasClicked ? 'Confirm' : 'Delete'}
              </Button>
            )}
          </Mutation>
        </ListItem>
        <Typography
          style={{
            margin: '10px 0px',
            fontSize: 20
          }}
          variant="subtitle1"
        >
          Swimlanes
        </Typography>
        {props.project.swimlaneOrder
          .map(swimlaneId => props.project.swimlanes[swimlaneId])
          .map(swimlane => (
            <div key={swimlane.id} style={{ display: 'flex' }}>
              <TextField
                fullWidth
                onChange={e => {
                  console.log('nope')
                  /* props.editSwimlane(
                    { ...swimlane, name: e.target.value },
                    props.project.id
                  ) */
                }}
                value={swimlane.name}
                style={{ marginRight: 16 }}
              />
              <IconButton
                onClick={() => {
                  console.log('delete that swimlane')
                }}
              >
                <Delete />
              </IconButton>
            </div>
          ))}
        <IconButton
          onClick={() => {
            console.log('create that swimlane')
          }}
          style={{ backgroundColor: '#3f51b5' }}
        >
          <Add style={{ color: 'white' }} />
        </IconButton>
      </DialogContent>
      <DialogActions>
        <Button onClick={props.onClose}>Continue</Button>
      </DialogActions>
    </Dialog>
  )
}

const actionCreators = {
  setProject: setProjectA,
  setSwimlane: setSwimlaneA
}

export const ProjectSettings = connect(
  null,
  actionCreators
)(CProjectSettings)
