import * as React from 'react'

import {
  withStyles,
  Paper,
  WithStyles,
  Avatar,
  Typography,
  Button,
  TextField,
  CircularProgress
} from '@material-ui/core'
import { formStyles } from '../styles/formStyles'
import { CalendarTodayRounded } from '@material-ui/icons'
import { connect } from 'react-redux'
import { setProjectA } from '../../store/actions/project'
import { Change } from '../../types/types'
import { TState } from '../../types/state'
import Helmet from 'react-helmet'
import { useState } from 'react'

import { Mutation, MutationResult } from 'react-apollo'
import {
  CreateProjectMutation,
  CreateProjectMutationVariables
} from '../../graphql/types'
import { openSnackbarA } from '../../store/actions/snackbar'
import { GQL_CREATE_PROJECT } from '../../graphql/mutations/project'

type CreateProjectProps = WithStyles<typeof formStyles> &
  typeof actionCreators &
  ReturnType<typeof mapState>

const CCreateProject = (props: CreateProjectProps) => {
  const [name, setName] = useState('')

  const { classes } = props

  return (
    <>
      <Helmet>
        <style type="text/css">{` body { background-color: #1d364c; }`}</style>
        <meta
          name="description"
          content="Create a project today to jumpstart your time"
        />
      </Helmet>
      <main style={{ margin: 'auto' }} className={classes.layout}>
        <Mutation
          mutation={GQL_CREATE_PROJECT}
          onError={error => {
            props.openSnackbar('Error when Creating Project, yikes!', 'error')
          }}
          onCompleted={data => {
            console.log(data)

            if (data.createProject && data.createProject.id) {
              props.createProject({
                id: data.createProject.id,
                newProj: data.createProject
              })

              console.log('oof')

              window.location.hash = '#/project/' + data.createProject!.id
              props.openSnackbar('Project Created Successfully', 'success')
            } else {
              props.openSnackbar('Project Could Not Be Created', 'warning')
            }
          }}
        >
          {(
            createProject: (args: {
              variables: CreateProjectMutationVariables
            }) => any,
            result: MutationResult<CreateProjectMutation>
          ) => {
            return (
              <form
                onSubmit={e => {
                  e.preventDefault()

                  if (props.user) {
                    createProject({
                      variables: {
                        name: name || 'Unnamed Project'
                      }
                    })
                  } else {
                    props.openSnackbar(
                      "You havn't logged in, silly goose",
                      'warning'
                    )
                  }
                }}
              >
                <Paper className={classes.paper}>
                  <Avatar className={classes.avatar}>
                    <CalendarTodayRounded />
                  </Avatar>
                  <Typography style={{ fontSize: 17 }}>
                    Create Project
                  </Typography>
                  {result.loading && (
                    <CircularProgress style={{ margin: '4px auto' }} />
                  )}
                  <div style={{ display: 'flex', width: '100%' }}>
                    <TextField
                      autoFocus
                      label="Project Name"
                      value={name}
                      fullWidth
                      onChange={(e: Change) => setName(e.target.value)}
                      required
                    />
                    <div style={{ marginLeft: 12, marginTop: 'auto' }}>
                      <Button
                        style={{ marginTop: 'auto' }}
                        variant="contained"
                        color="secondary"
                        type="submit"
                      >
                        Create
                      </Button>
                    </div>
                  </div>
                </Paper>
              </form>
            )
          }}
        </Mutation>
      </main>
    </>
  )
}

const mapState = (state: TState) => ({
  projects: state.projects,
  user: state.user
})
const actionCreators = {
  createProject: setProjectA,
  openSnackbar: openSnackbarA
}

export const CreateProject = connect(
  mapState,
  actionCreators
)(withStyles(formStyles)(CCreateProject))
