import React from 'react'
import {
  Theme,
  createStyles,
  withStyles,
  WithStyles,
  Fab
} from '@material-ui/core'
import { connect } from 'react-redux'
import { TState } from '../../types/state'
import { Add } from '@material-ui/icons'
import { GoalModal } from './GoalModal'

/**
 * let them have a quantity of the amount of that item they want
 * @todo make a seperate page with purchased items, where they can check them off as used.
 * @todo undo purchasing items and get your money back
 * @todo filter appbar for what you can afford and with a slider
 */

const styles = (theme: Theme) =>
  createStyles({
    fab: {
      position: 'fixed',
      bottom: theme.spacing(2),
      right: theme.spacing(2),
      zIndex: 999
    }
  })

type TProps = WithStyles<typeof styles> &
  ReturnType<typeof mapState> &
  typeof actionCreators

const CPointStore = (props: TProps) => {
  const [creating, setCreating] = React.useState(false)

  return (
    <div>
      <Fab
        color="primary"
        className={props.classes.fab}
        onClick={() => setCreating(true)}
      >
        <Add />
      </Fab>
      {creating && (
        <GoalModal creating handleClose={() => setCreating(false)} />
      )}
    </div>
  )
}

const mapState = (state: TState) => ({
  goals: state.goals
})

const actionCreators = {}

export const Goals = connect(
  mapState,
  actionCreators
)(withStyles(styles)(CPointStore))
