import React from 'react'
import { TTask } from '../../types/task'
import { Typography, TextField, Button } from '@material-ui/core'

type TProps = {
  task: TTask
  extraHours: number
  extraMinutes: number
  setExtraHours: (extra: number) => void
  setExtraMinutes: (extra: number) => void
  setTask: (newTask: TTask) => void
}

export const TaskModalManualTimeEntry = (props: TProps) => {
  const {
    task,
    extraHours,
    extraMinutes,
    setExtraHours,
    setExtraMinutes
  } = props

  return (
    <>
      <Typography
        style={{
          margin: '6px 0px',
          fontSize: 18
        }}
        variant="subtitle1"
      >
        Manual Time Entry
      </Typography>
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-end',
          flex: '0 0 auto'
        }}
      >
        <TextField
          label="hours"
          fullWidth
          style={{ margin: '0 4px' }}
          value={extraHours}
          type="number"
          onChange={e => {
            setExtraHours(parseInt(e.target.value))
          }}
        />
        <TextField
          style={{ margin: '0 4px' }}
          label="minutes"
          fullWidth
          value={extraMinutes}
          type="number"
          onChange={e => setExtraMinutes(parseInt(e.target.value))}
        />
        <Button
          style={{ margin: 'auto 4px 0px 4px', minWidth: 100 }}
          variant="outlined"
          onClick={() => {
            props.setTask({
              ...task,
              timeWorkedOn:
                task.timeWorkedOn + extraMinutes * 60 + extraHours * 3600
            })
          }}
        >
          Confirm
        </Button>
      </div>
    </>
  )
}
