import { ColumnFieldsFragment } from './../../graphql/types'

export const setColumnA = (args: {
  id: string
  projectId: string
  newCol: Partial<ColumnFieldsFragment> | null
}) => ({
  type: 'SET_COLUMN',
  id: args.id,
  projectId: args.projectId,
  newColumn: args.newCol
})

export type ColumnAction = ReturnType<typeof setColumnA>
