import React from 'react'
import { Doughnut } from 'react-chartjs-2'
import { Typography } from '@material-ui/core'
import { connect } from 'react-redux'
import { TState } from '../../types/state'
import { colors } from '../../colors'
import { getAllTasks } from '../../utils/utilities'

const CHomeBoard = (props: ReturnType<typeof mapState>) => {
  const { tasks } = props

  const taskData = {
    labels: Object.values(tasks).map(task => task.name),
    datasets: [
      {
        label: 'Pomodoro Time By Task',
        data: Object.values(tasks).map(task =>
          Math.floor(task.timeWorkedOn / 60)
        ) as number[],
        backgroundColor: Object.values(colors),
        hoverBackgroundColor: Object.values(colors)
      }
    ]
  }

  const hasTimeLoggedOnTasks =
    taskData.datasets[0].data.filter(item => item).length !== 0

  return (
    <>
      <Typography variant="h4" align="center">
        {props.state.goals.points} Points🎉
      </Typography>
      <div
        style={{
          marginBottom: 40,
          marginTop: 20,
          maxWidth: 1000,
          marginLeft: 'auto',
          marginRight: 'auto'
        }}
      >
        {hasTimeLoggedOnTasks && (
          <div style={{ maxWidth: 800, margin: '0px auto' }}>
            <Typography align="center" style={{ fontSize: 22 }} variant="h4">
              Pomodoro Time by Task
            </Typography>
            <Doughnut data={taskData} />
          </div>
        )}
      </div>
    </>
  )
}

const mapState = (state: TState) => ({
  tasks: getAllTasks(state.projects),
  pomodoro: state.pomodoro,
  state
})

export const HomeBoard = connect(mapState)(CHomeBoard)
