import React from 'react'
import { Typography, IconButton, Chip } from '@material-ui/core'
import { Edit, Close, Add } from '@material-ui/icons'
import { TTask } from '../../types/task'
import { TTag, TTags } from '../../types/state'

type TProps = {
  setAnchor: (newAnchor: HTMLElement) => void
  setTask: (newTask: TTask) => void
  task: TTask
  tags: TTags
}

export const TaskModalTags = (props: TProps) => {
  const handleChip = (tag: TTag) => {
    let newTags = [...props.task.tags]

    if (newTags.includes(tag.id)) {
      newTags = newTags.filter(id => id !== tag.id)
    } else {
      newTags.push(tag.id)
    }

    props.setTask({ ...props.task, tags: newTags })
  }

  return (
    <div>
      <Typography
        style={{
          margin: '6px 0px',
          fontSize: 18
        }}
        variant="subtitle1"
      >
        Tags
      </Typography>
      <IconButton
        onClick={e => props.setAnchor(e.currentTarget)}
        style={{ display: 'inline-block' }}
      >
        <Edit />
      </IconButton>
      {Object.values(props.tags).map(tag => {
        const isTasksTag = props.task.tags.includes(tag.id)
        if (tag) {
          return (
            <Chip
              variant={isTasksTag ? 'default' : 'outlined'}
              key={tag.id}
              style={{
                margin: '0px 4px',
                backgroundColor: isTasksTag ? tag.color : 'white'
              }}
              label={tag.name}
              deleteIcon={isTasksTag ? <Close /> : <Add />}
              onDelete={() => handleChip(tag)}
              onClick={() => handleChip(tag)}
            />
          )
        }
        return null
      })}
    </div>
  )
}
