import React, { useState } from 'react'
import {
  withStyles,
  WithStyles,
  TextField,
  DialogTitle,
  DialogActions,
  Button,
  DialogContent,
  Select,
  MenuItem,
  Dialog
} from '@material-ui/core'
import { formStyles } from '../styles/formStyles'
import { connect } from 'react-redux'
import { TGoalType } from '../../types/state'
import { addDays } from 'date-fns'
import { DatePicker } from 'material-ui-pickers'
import { KeyboardArrowLeft, KeyboardArrowRight } from '@material-ui/icons'

type TProps = WithStyles<typeof formStyles> & {
  handleClose: () => void
  creating: boolean
} & typeof actionCreators

const CCreateItem = withStyles(formStyles)((props: TProps) => {
  const [name, setName] = useState('')
  const [type, setType] = useState(undefined as undefined | TGoalType)
  const [payload, setPayload] = useState(0)
  const [due, setDue] = useState(addDays(new Date(), 7))

  return (
    <div>
      <Dialog open onClose={() => props.handleClose()}>
        <DialogTitle>{`Create Goal`}</DialogTitle>
        <DialogContent style={{ minWidth: 500 }}>
          <TextField
            autoFocus
            value={name}
            fullWidth
            required
            margin="dense"
            onChange={e => setName(e.target.value)}
            label="Name"
            style={{ marginBottom: 12 }}
          />

          <div>
            <DatePicker
              fullWidth
              leftArrowIcon={<KeyboardArrowLeft />}
              rightArrowIcon={<KeyboardArrowRight />}
              label="End Date"
              value={due}
              onChange={(date: Date) => {
                setDue(date)
              }}
            />
          </div>

          <Select
            style={{ marginTop: 12 }}
            fullWidth
            value={type}
            onChange={e => {
              setType(e.target.value as any)
            }}
          >
            <MenuItem value={'point'}>Points</MenuItem>
            <MenuItem value={'time'}>Time</MenuItem>
          </Select>
          {type && (
            <TextField
              value={payload}
              type="number"
              fullWidth
              required
              margin="dense"
              onChange={e => setPayload(parseInt(e.target.value))}
              label={
                type === 'time' ? 'Amount of Hours Required' : 'Points Required'
              }
            />
          )}
        </DialogContent>
        <DialogActions>
          <Button fullWidth onClick={() => props.handleClose()}>
            Cancel
          </Button>
          <Button
            onClick={() => {
              props.handleClose()
            }}
            disabled={!type || !payload || !name}
            fullWidth
            variant="outlined"
            type="submit"
            color="primary"
          >
            Create
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
})

const actionCreators = {}

export const GoalModal = connect(
  null,
  actionCreators
)(CCreateItem)
