import { TaskFieldsFragment } from '../../graphql/types'

export const setTaskA = (args: {
  id: string
  newTask: TaskFieldsFragment | null
  projectId: string
}) => ({
  type: 'SET_TASK',
  id: args.id,
  projectId: args.projectId,
  newTask: args.newTask
})

export type TaskAction = ReturnType<typeof setTaskA>
