import React, { useState } from 'react'
import { TTask } from '../../types/task'
import { TTags } from '../../types/state'
import { Popover, Chip, Typography, TextField, Button } from '@material-ui/core'
import { DeleteForever } from '@material-ui/icons'
import { ChooseColor } from '../utils/chooseColor'
import { colors } from '../../colors'

/* const styles = makeStyles({
  root: {}
}) */
type OwnProps = {
  task: TTask
  open: boolean
  projectId: string
  selectTags?: (newTags: TTags) => void
  handleClose: () => void
  deleteTag: (id: string) => void
  tags: TTags
  anchorEl: HTMLElement | null
  createTag: (name: string, color: string) => void
}

type TProps = OwnProps

export const CreateTag = (props: TProps) => {
  // const classes = styles()
  const [newName, setNewName] = useState('')
  const [newColor, setNewColor] = useState(colors.White)
  const { open, handleClose, anchorEl } = props

  return (
    <Popover
      anchorEl={anchorEl}
      open={open}
      onClose={handleClose}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'left'
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'center'
      }}
    >
      <div
        style={{
          padding: 10,
          display: 'flex',
          flexDirection: 'column',
          minWidth: Math.min(500, window.innerWidth / 2)
        }}
      >
        {Object.values(props.tags).map(tag => {
          if (tag) {
            return (
              <Chip
                key={tag.id}
                clickable
                label={tag.name}
                deleteIcon={<DeleteForever style={{ marginLeft: 'auto' }} />}
                onDelete={() => props.deleteTag(tag.id)}
                style={{
                  backgroundColor: tag.color,
                  margin: '4px 8px'
                }}
              />
            )
          }
          return null
        })}
        <Typography
          style={{
            margin: '6px 0px',
            fontSize: 18
          }}
          variant="subtitle1"
        >
          Create Tag
        </Typography>
        <TextField
          style={{ marginBottom: 16 }}
          fullWidth
          value={newName}
          onChange={e => setNewName(e.target.value)}
          label="Tag Name"
        />
        <ChooseColor
          onChange={(color: string) => setNewColor(color)}
          color={newColor}
        />
        <Button
          style={{ marginTop: 10 }}
          fullWidth
          onClick={() => props.createTag(newName, newColor)}
          variant="contained"
          color="primary"
        >
          Create {newName || 'Tag'}
        </Button>
      </div>
    </Popover>
  )
}
